import React, { Component } from 'react'
import { connect } from 'react-redux'

class About extends Component {

  static async getInitialProps({ store, isServer, pathname, query }) {
    const login = store.getState().login
    store.dispatch({type:'SUCCESS',login:'Standbymik'})
    const login2 = store.getState().login
    return {
      login,
      login2
    }

  }

  dispatchLogin = () => {
    this.props.onLogin()
  }

  render() {

    const { login , login2 } = this.props
    console.log(login,login2)
    this.dispatchLogin

    return (
      <div style={{ fontSize: 60, color: '#ff00ff' }}>
        hahaa
      </div>

    );
  }
}

const mapStateToProps = (state) => {
  return { login: state.login }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onLogin: () => {
      dispatch({ type: 'SUCCESS', login: 'Standbymik' })
    }
  }
}
export default connect(mapStateToProps, mapDispatchToProps)(About)
